body {
  margin: 0;
}
main {
  flex-grow: 1;
}
img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
a {
  color: inherit;
  text-decoration: none;
}
.helvetica-700 {
  font-family: "Helvetica-Bold", "Arial", sans-serif;
  font-weight: 700;
}
.helvetica {
  font-family: "Helvetica", "Arial", sans-serif;
  font-weight: 400;
}
.reset-btn {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.hover {
  transition: color 0.3s;
}
.hover:hover {
  color: #000000;
  transition: color 0.1s;
}
.align-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.align-top {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.align-left {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
@font-face {
  font-family: "Helvetica";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Helvetica.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Helvetica-Bold";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Helvetica-Bold.woff2") format("woff2");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.article {
  display: grid;
  grid-template-columns: auto 1fr 1fr;
  grid-template-rows: repeat(3, min-content);
  column-gap: 0.75rem;
  padding: 1rem 0;
  border-bottom: 1px solid #DEDEDE;
}
.article__picture {
  grid-column: 1/2;
  grid-row: 1/4;
  width: 80px;
  height: 80px;
  object-fit: cover;
}
.article__title {
  grid-column: 2/3;
  grid-row: 1/2;
}
.article__price {
  grid-column: 3/4;
  grid-row: 1/2;
  justify-self: end;
}
.article__text {
  display: none;
}
.article__container-button {
  grid-column: 2/3;
  grid-row: 3/4;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 81px;
  padding: 0.5rem;
  border: 1px solid #000000;
}
.article__button {
  position: relative;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  width: 15px;
  height: 15px;
}
.article__button::before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  content: '';
  width: 15px;
  height: 2px;
  background-color: #000000;
}
.article__button--add::after {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  content: '';
  width: 2px;
  height: 15px;
  background-color: #000000;
}
.article__link {
  grid-column: 3/4;
  grid-row: 3/4;
  justify-self: end;
  align-self: end;
  font-size: 0.75rem;
  line-height: calc(14/12);
  color: #9F9F9F;
  text-decoration: underline;
  text-underline-offset: 2px;
}
@media (min-width: 1920px) {
  .article {
    display: grid;
    column-gap: 1.25rem;
    row-gap: 0.625rem;
    width: 93%;
    padding: 1.5rem 0;
  }
  .article__picture {
    width: 150px;
    height: 150px;
  }
  .article__text {
    display: block;
    grid-column: 2/3;
    grid-row: 2/3;
    margin: 0;
    color: #9F9F9F;
  }
  .article__container-button {
    width: 114px;
  }
  .article__link {
    font-size: 0.875rem;
    line-height: calc(16/14);
    transition: color 0.3s;
  }
  .article__link:hover {
    color: #000000;
    transition: color 0.1s;
  }
}
.breadcrumbs {
  padding: 2.56rem 0 0;
}
.breadcrumbs__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}
.breadcrumbs__item {
  font-size: 1.125rem;
  line-height: calc(20/18);
}
.breadcrumbs__item + .breadcrumbs__item {
  margin-left: 0.625rem;
}
.breadcrumbs__item:first-of-type {
  color: #9F9F9F;
}
.breadcrumbs__item:first-of-type::after {
  content: '/';
  margin-left: 0.625rem;
}
@media (min-width: 1920px) {
  .breadcrumbs {
    padding: 3.125rem 0 1rem;
  }
}
.burger {
  top: 1rem;
  left: 1rem;
  z-index: 10;
  display: block;
  width: 28px;
  height: 20px;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
.burger__inner,
.burger__inner::before,
.burger__inner::after {
  display: block;
  position: absolute;
  width: 28px;
  height: 2px;
  background-color: #000000;
}
.burger__inner::before {
  content: "";
  top: -10px;
}
.burger__inner::after {
  content: "";
  bottom: -10px;
}
@media (min-width: 1920px) {
  .burger {
    display: none;
  }
}
.button {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  padding: 1.1rem;
  font-family: "Helvetica-Bold", "Arial", sans-serif;
  font-weight: 700;
  color: #000000;
  font-size: 1.25rem;
  line-height: calc(23/20);
  border: 1px solid #000000;
}
.cart {
  position: relative;
}
.cart__group {
  margin: 0;
  padding: 0;
  border: none;
}
.cart__text {
  margin: 0;
  padding: 0.75rem 0 3.2rem;
}
.cart__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.cart__link {
  color: #9F9F9F;
  font-size: 1rem;
  line-height: calc(18/16);
  text-decoration: underline;
  transition: color 0.3s;
}
.cart__link:hover {
  color: #000000;
  transition: color 0.1s;
}
.cart__item {
  position: relative;
}
.cart__item + .cart__item {
  margin-top: 1.2rem;
}
.cart__item:first-of-type {
  margin-bottom: 244px;
}
.cart__input {
  width: 100%;
  min-height: 42px;
  font-size: 1rem;
  line-height: calc(19/16);
  resize: none;
}
.cart__label {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0, -50%);
  color: #9F9F9F;
}
.cart__input:focus + .cart__label,
.cart__input:not(:placeholder-shown) + .cart__label {
  transform: translate(-10%, -40px) scale(0.8);
  transition: transform 0.2s, scale 0.2s;
}
.cart__subtitle {
  margin: 2.3rem 0 1rem;
}
.cart__total {
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 1.4rem 0;
  font-family: "Helvetica-Bold", "Arial", sans-serif;
  font-weight: 700;
  font-size: 1.625rem;
  line-height: calc(30/26);
}
.cart__select option[value=''] {
  display: none;
}
.cart__button {
  width: 100%;
}
@media (min-width: 1920px) {
  .cart {
    display: flex;
  }
  .cart.container {
    padding-top: 0;
  }
  .cart__wrapper {
    width: 46%;
    margin-right: 3.125rem;
  }
  .cart__text {
    padding-bottom: 2.2rem;
  }
  .cart__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 2.1rem;
  }
  .cart__item:first-of-type {
    margin-bottom: 0;
  }
  .cart__item:not(.cart__item#nameItem):not(.cart__item#telItem) {
    grid-column: 1/3;
  }
  .cart__item#nameItem {
    grid-column: 1/2;
  }
  .cart__item#telItem {
    grid-column: 2/3;
  }
  .cart__subtitle {
    margin: 4.2rem 0 0.8rem;
  }
  .cart__total {
    position: absolute;
    top: 567px;
    right: 25.3rem;
    width: 557px;
    padding: 2.8rem 1.8rem;
    color: #FCFCFC;
    font-size: 2.5rem;
    line-height: calc(46/40);
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 2;
  }
  .cart__button {
    transition: color 0.3s, background-color 0.3s;
  }
  .cart__button:hover {
    color: #FCFCFC;
    background-color: #000000;
    transition: color 0.1s, background-color 0.1s;
  }
}
.container {
  padding: 1rem 1rem 2.5rem;
}
@media (min-width: 1920px) {
  .container {
    padding: 2.1rem 25.3rem 4.3rem;
  }
}
h1 {
  margin: 0;
  font-family: "Helvetica-Bold", "Arial", sans-serif;
  font-weight: 700;
  font-size: 1.875rem;
  line-height: calc(34/30);
}
@media (min-width: 1920px) {
  h1 {
    font-size: 2.5rem;
    line-height: calc(46/40);
  }
}
h3,
.article__price,
.article__count {
  margin: 0;
  font-family: "Helvetica-Bold", "Arial", sans-serif;
  font-weight: 700;
  font-size: 1rem;
  line-height: calc(18/16);
}
@media (min-width: 1920px) {
  h3,
  .article__price,
  .article__count {
    font-size: 1.25rem;
    line-height: calc(23/20);
  }
}
h2 {
  margin: 0;
  font-family: "Helvetica-Bold", "Arial", sans-serif;
  font-weight: 700;
  font-size: 1.625rem;
  line-height: calc(30/26);
}
@media (min-width: 1920px) {
  h2 {
    font-size: 1.75rem;
    line-height: calc(32/28);
  }
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__label {
  display: block;
  width: 22px;
  height: 22px;
  margin-left: 1rem;
  mask-image: url('../images/svg-sprites/sprite.svg#search');
  mask-repeat: no-repeat;
  mask-size: cover;
  mask-position: center;
  background-color: #000000;
}
.header__input {
  display: none;
}
.header__link {
  display: block;
  mask-repeat: no-repeat;
  mask-size: cover;
  mask-position: center;
  background-color: #000000;
}
.header__link--logo {
  width: 77px;
  height: 24px;
  margin: 0 auto;
  mask-image: url('../images/svg-sprites/sprite.svg#logo');
}
.header__link--login {
  display: none;
}
.header__link--favourite {
  width: 25px;
  height: 22px;
  mask-image: url('../images/svg-sprites/sprite.svg#favourite');
}
.header__link--cart {
  position: relative;
  width: 22px;
  height: 24px;
  mask-image: url('../images/svg-sprites/sprite.svg#cart');
}
.header__link--cart::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 1rem;
  height: 1rem;
  background-color: #000000;
  border-radius: 50%;
}
.header__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}
.header__item {
  position: relative;
}
.header__item + .header__item {
  margin-right: 1rem;
}
.header__item:last-of-type {
  margin-right: 0.5rem;
}
.header__count {
  position: absolute;
  top: -18%;
  right: -15%;
  color: #FCFCFC;
  z-index: 1;
  font-size: 0.75rem;
  line-height: calc(14/12);
}
.header__count::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 1rem;
  height: 1rem;
  background-color: #000000;
  border-radius: 50%;
  z-index: -1;
}
@media (min-width: 1920px) {
  .header {
    position: relative;
  }
  .header.container {
    padding-bottom: 2.3rem;
  }
  .header__label {
    width: 25px;
    height: 25px;
    margin-left: 0;
  }
  .header__input {
    display: block;
    width: 250px;
    margin-left: 1.3rem;
    margin-right: auto;
    font-size: 1.125rem;
    line-height: calc(20/18);
    transition: color 0.3s;
  }
  .header__input::placeholder {
    font-size: 1.125rem;
    line-height: calc(20/18);
  }
  .header__input:focus::placeholder {
    color: #000000;
    transition: color 0.1s;
  }
  .header__item {
    margin-right: 1rem;
  }
  .header__link--logo {
    position: absolute;
    top: 34%;
    left: 50%;
    transform: translate(-50%, 0);
    width: 160px;
    height: 50px;
  }
  .header__link--login {
    display: block;
    width: 28px;
    height: 28px;
    mask-image: url('../images/svg-sprites/sprite.svg#login');
  }
  .header__link--favourite {
    width: 32px;
    height: 28px;
  }
  .header__link--cart {
    width: 28px;
    height: 28px;
  }
}
.input {
  min-width: 235px;
  padding: 0.375rem 0;
  border: none;
  border-bottom: 1px solid #9F9F9F;
  border-radius: 0;
  outline: none;
  font-family: "Helvetica", "Arial", sans-serif;
  font-weight: 400;
}
.input::placeholder {
  font-family: "Helvetica", "Arial", sans-serif;
  font-weight: 400;
  color: #9F9F9F;
}
.input:invalid {
  color: #D80909;
}
.invalid {
  color: #D80909;
}
.map {
  position: absolute;
  top: 200px;
  left: 50%;
  transform: translateX(-50%);
  width: calc(100vw - 2*1rem);
  height: 200px;
}
@media (min-width: 1920px) {
  .map {
    position: relative;
    top: 0;
    left: 0;
    transform: translateX(0);
    width: 100%;
    height: 700px;
  }
}
.nav {
  display: none;
}
@media (min-width: 1920px) {
  .nav {
    display: flex;
    justify-content: space-between;
    padding: 4.3rem 0 0;
  }
  .nav__list {
    display: flex;
    margin: 0;
    padding: 0;
    list-style: none;
  }
  .nav__list:first-of-type {
    counter-reset: section;
  }
  .nav__list:first-of-type .nav__link::after {
    content: " " counter(section);
    counter-increment: section;
  }
  .nav__list:last-of-type {
    counter-reset: section 3;
  }
  .nav__list:last-of-type .nav__link::after {
    content: " " counter(section);
    counter-increment: section;
  }
  .nav__item + .nav__item {
    margin-left: 1.875rem;
  }
  .nav__link {
    font-family: "Helvetica-Bold", "Arial", sans-serif;
    font-weight: 700;
    font-size: 1.125rem;
    line-height: calc(20/18);
    color: #9F9F9F;
    text-transform: uppercase;
    transition: color 0.3s;
  }
  .nav__link:hover {
    color: #000000;
    transition: color 0.1s;
  }
}
.page {
  height: 100%;
}
.page__body {
  min-width: 320px;
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  font-family: "Helvetica", "Arial", sans-serif;
  font-weight: 400;
  font-size: 1rem;
  line-height: calc(19/16);
}
.select {
  display: inline-block;
  vertical-align: top;
  width: 100%;
}
.select__gap {
  position: relative;
  min-height: 42px;
  padding: 0.8rem 0;
  color: #9F9F9F;
  border-bottom: 1px solid #9F9F9F;
  cursor: pointer;
}
.on.select__gap,
.select__gap:hover {
  color: #000000;
}
.select__list {
  margin: 0;
  padding: 0;
  list-style: none;
  padding: 1.25rem 1.25rem 0;
  border: 1px solid #9F9F9F;
  border-top: none;
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
}
.select__list.on {
  display: block;
}
.select__item span {
  display: block;
  padding-bottom: 1.25rem;
  color: #9F9F9F;
  cursor: pointer;
}
.select__item.selected,
.select__item span:hover {
  color: #000000;
}
.select__gap:after {
  content: '';
  display: block;
  width: 15px;
  height: 10px;
  position: absolute;
  top: 50%;
  right: 3px;
  background-image: url('../images/svg-sprites/sprite.svg#select');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  transition: all 0.2s ease-in-out;
  transform: translateY(-50%) rotate(0deg);
}
.on.select__gap:after {
  transform: translateY(-50%) rotate(180deg);
}
@media (min-width: 1920px) {
  .select__gap {
    padding: 1rem 0;
  }
}
